@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  @apply fixed top-0 left-0 w-full bg-white shadow-lg px-8 py-6 rounded-b-lg z-50
}

.link {
  @apply text-gray-400 font-semibold mx-4 hover:text-yellow-500 hover:bg-yellow-100 px-4 py-2 tracking-wider rounded-full transition duration-300 ease-in-out
}
